import React, { useContext, useEffect, useState } from "react";
import { store } from "store/store";
import "./ArticleDetail.css";
import ArticleCategory from "components/Articles/Articles__Category";
import MarkdownRenderer from "../components/Markdown/MarkdownRenderer";

function ArticleDetail({ match }) {
  // 전역 상태 관리 (store)
  const globalState = useContext(store);
  const { dispatch } = globalState;

  // Loading
  const [isLoading, setIsLoading] = useState(true);

  useEffect(() => {
    dispatch({ type: "GET_ARTICLES" });
    setIsLoading(false);
  }, [dispatch]);

  return (
    <div>
      {isLoading ? (
        <div className="loader">
          <span className="loader_text">Loading...</span>
        </div>
      ) : (
        <div className="ArticleDetail">
          {/* 왼쪽 */}
          <div className="ArticleDetail__left">
            <ArticleCategory />
          </div>
          {/* 오른쪽 */}
          <div className="ArticleDetail__right">
            <MarkdownRenderer
              category={match.url.split("/")[2]}
              articleId={match.url.split("/")[3]}
            />
          </div>
        </div>
      )}
    </div>
  );
}

export default ArticleDetail;
