import React from "react";
import { Link } from "react-router-dom";
import "./Header.css";

function Header() {
  return (
    <div className="Header">
      <div className="Header__styling"></div>
      <Link to="/" className="Header__word">
        Home
      </Link>
      <Link to="/profile" className="Header__word">
        Profile
      </Link>
      <Link to="/articles" className="Header__word">
        Articles
      </Link>
      <Link to="/archives" className="Header__word">
        Archives
      </Link>
      <div className="Header__styling--Reverse" />
    </div>
  );
}

export default Header;
