import React from "react";
import "./ArchivedItem.css";
import ArchivedItemImgCarousel from "./ArchivedItem__ImgCarousel";

function ArchivedItem({ archive }) {
  // Method
  // Toggle Item
  const showItem = event => {
    const target = event.target.getAttribute("value");
    const scrollHeight = Array.from(
      document.querySelectorAll(`#${target} .Archived-item__detail`)
    ).reduce((acc, curr) => acc + curr.scrollHeight + 5, 0);

    const itemHeight = document.querySelector(`#${target} .Archived-item--show`)
      .style.height;

    if (itemHeight) {
      document.querySelector(`#${target} .Archived-item--show`).style.height =
        null;
    } else {
      document.querySelector(
        `#${target} .Archived-item--show`
      ).style.height = `${scrollHeight + 1}px`;
    }
  };

  return (
    <div className="Archived-item" id={`archive_${archive.id}`}>
      <h3
        className="Archived-item__title"
        onClick={showItem}
        value={`archive_${archive.id}`}
      >
        - {archive.title}
      </h3>
      <div className="Archived-item--show">
        {archive.details.map((detail, idx) => (
          <div
            className="Archived-item__detail"
            id={`archive_${archive.id}_${idx}`}
            key={idx}
          >
            <div className="Archived-item__date">{detail.date}</div>
            <ArchivedItemImgCarousel
              images={detail.images}
              archiveId={archive.id}
              itemIdx={idx}
            />
            <div className="Archived-item__content">
              {detail.content.split("\n").map((element, idx) => (
                <div key={idx}>{element}</div>
              ))}
            </div>
            <hr />
          </div>
        ))}
      </div>
    </div>
  );
}

export default ArchivedItem;
