import React, { useContext, useEffect, useState } from "react";
import ReactMarkdown from "react-markdown";
import remarkGfm from "remark-gfm";
import { Prism as SyntaxHighlighter } from "react-syntax-highlighter";
import { nord } from "react-syntax-highlighter/dist/esm/styles/prism";
import "./MarkdownRenderer.css";
import { store } from "store/store";

function MarkdownRenderer({ category, articleId }) {
  // 전역 상태 관리 (store)
  const globalState = useContext(store);
  const { value, dispatch } = globalState;
  const { articles } = value;

  // Loading
  const [isLoading, setIsLoading] = useState(true);

  // Markdown file
  const [markdown, setMarkdown] = useState("");
  const [documentTitle, setDocumentTitle] = useState("");
  const [path, setPath] = useState("");

  useEffect(() => {
    dispatch({ type: "GET_ARTICLES" });
  }, [dispatch]);

  useEffect(() => {
    // 파일 주소 찾기
    articles.categoryList.forEach(categoryElement => {
      if (categoryElement.category === category) {
        categoryElement.itemList.forEach(articleElement => {
          if (articleElement.id === parseInt(articleId)) {
            setPath(articleElement.content);
            setDocumentTitle(articleElement.title);
          }
        });
      }
    });
  }, [articles.categoryList, articleId, category]);

  useEffect(() => {
    if (path) {
      const readmePath = require(`store/article_data/${path.split("/")[2]}/${
        path.split("/")[3]
      }`);

      // 파일 가져오기
      fetch(readmePath.default)
        .then(r => r.text())
        .then(text => {
          setMarkdown(text);
        });
      setIsLoading(false);
    }
  }, [markdown, path]);

  return (
    <div>
      {isLoading ? (
        <div className="loader">
          <span className="loader_text">Loading...</span>
        </div>
      ) : (
        <div className="MarkdownRenderer">
          <h1 className="MarkdownRenderer__Title">{documentTitle}</h1>
          <ReactMarkdown
            remarkPlugins={[remarkGfm]}
            children={markdown
              .replace(/\n\s\n\s/gi, "\n\n &nbsp;")
              .replace(/\*\*/gi, "@$_%!^")
              .replace(/\**\*/gi, "/")
              .replace(/@\$_%!\^/gi, "**")
              .replace(/<\/?u>/gi, "*")}
            components={{
              code({ node, inline, className, children, ...props }) {
                const match = /language-(\w+)/.exec(className || "");
                return inline ? (
                  // 강조 (``)
                  <code
                    style={{
                      background: "var(--highlight-color)",
                      padding: "2px",
                    }}
                    {...props}
                  >
                    {children}
                  </code>
                ) : match ? (
                  // 코드 (```)
                  <SyntaxHighlighter
                    children={String(children).replace(/\n$/, "")}
                    style={nord}
                    language={match[1]}
                    PreTag="div"
                    {...props}
                  />
                ) : (
                  <SyntaxHighlighter
                    children={String(children).replace(/\n$/, "")}
                    style={nord}
                    language="textile"
                    PreTag="div"
                    {...props}
                  />
                );
              },
              // 인용문 (>)
              blockquote({ node, children, ...props }) {
                return (
                  <div
                    style={{
                      background: "#f0f0f0",
                      padding: "1px 15px",
                      borderRadius: "10px",
                    }}
                    {...props}
                  >
                    {children}
                  </div>
                );
              },
            }}
          />
        </div>
      )}
    </div>
  );
}

export default MarkdownRenderer;
