import React, { useContext, useEffect, useState } from "react";
import "./Profile.css";
import { store } from "store/store";

function Profile() {
  // 전역 상태 관리 (store)
  const globalState = useContext(store);
  const { value, dispatch } = globalState;
  const { profile } = value;

  // Loading
  const [isLoading, setIsLoading] = useState(true);

  useEffect(() => {
    dispatch({ type: "GET_PROFILE" });
    setIsLoading(false);
  }, [dispatch]);

  return (
    <div>
      {isLoading ? (
        <div className="loader">
          <span className="loader_text">Loading...</span>
        </div>
      ) : (
        <div className="Profile">
          <h1>Profile</h1>
          <h2>소개</h2>
          <div className="Profile__introduction">
            {profile.introduction.split("\n").map((element, idx) => (
              <p key={idx}>{element}</p>
            ))}
          </div>
          <h2>기술스택</h2>
          <div className="Profile__tech_stack">
            <h3>Front-end</h3>
            <div>
              {profile.tech_stack.frontend.map((element, idx) => (
                <img
                  src={element}
                  className="Profile__tech_stack-logo"
                  alt="tech_stack"
                  key={idx}
                />
              ))}
            </div>
            <h3>Back-end</h3>
            <div>
              {profile.tech_stack.backend.map((element, idx) => (
                <img
                  src={element}
                  className="Profile__tech_stack-logo"
                  alt="tech_stack"
                  key={idx}
                />
              ))}
            </div>
            <h3>Version Control</h3>
            <div>
              {profile.tech_stack.version_control.map((element, idx) => (
                <img
                  src={element}
                  className="Profile__tech_stack-logo"
                  alt="tech_stack"
                  key={idx}
                />
              ))}
            </div>
            <h3>Studying</h3>
            <div>
              {profile.tech_stack.studying.map((element, idx) => (
                <img
                  src={element}
                  className="Profile__tech_stack-logo"
                  alt="tech_stack"
                  key={idx}
                />
              ))}
            </div>
          </div>
          <h2>경력</h2>
          <div className="Profile__career">
            {profile.career.map((element, idx) => (
              <div key={idx}>
                <div className="Profile__career-name">{element.name}</div>
                <div className="Profile__career-content">{element.content}</div>
              </div>
            ))}
          </div>
        </div>
      )}
    </div>
  );
}

export default Profile;
