import React, { useContext, useEffect, useState } from "react";
import { Link } from "react-router-dom/cjs/react-router-dom.min";
import { store } from "../store/store";
import "./Home.css";

function Home() {
  // 전역 상태 관리 (store)
  const globalState = useContext(store);
  const { value, dispatch } = globalState;
  const { home } = value;

  // Loading
  const [isLoading, setIsLoading] = useState(true);

  useEffect(() => {
    dispatch({ type: "GET_HOME" });
    setIsLoading(false);
  }, [dispatch]);

  return (
    <div>
      {isLoading ? (
        <div className="loader">
          <span className="loader_text">Loading...</span>
        </div>
      ) : (
        <div className="Home">
          {/* 왼쪽 */}
          <div className="Home__left">
            <div>
              <div className="Home__profile-img">
                {/* 프로필 이미지 */}
                <img src={home.profile_img} alt="profile" />
              </div>
              <div className="Home__profile-content">
                <h3>박정웅</h3>
                <div>
                  {home.summary.split("\n").map((element, idx) =>
                    element[0] === "\t" ? (
                      <div key={idx} className="Home__profile-content-detail">
                        {element}
                      </div>
                    ) : (
                      <div key={idx} className="Home__profile-content-list">
                        {element}
                      </div>
                    )
                  )}
                </div>
              </div>
            </div>
          </div>
          {/* 오른쪽 */}
          <div className="Home__right">
            <div>
              <p>
                <strong>{home.welcome_word}</strong>
              </p>
              <hr className="Horizontal_Line" />
              <p>
                <strong>소개글</strong>
              </p>
              <div>
                {home.introduction.split("\n").map((element, idx) => (
                  <p key={idx}>{element}</p>
                ))}
              </div>
              <hr className="Horizontal_Line" />
              <div>
                <Link to="/profile" className="link_tag">
                  자세히 보러가기
                </Link>
              </div>
            </div>
          </div>
        </div>
      )}
    </div>
  );
}

export default Home;
